import React, { Component } from 'react';
import { Link,hashHistory } from 'react-router';

class Test extends Component {
	componentDidMount(){
		 this.props.router.setRouteLeaveHook(
        this.props.route, 
        this.leave
      )
	}
	leave(nextLocation) {
      // 返回 false 会继续停留当前页面，
      // 否则，返回一个字符串，会显示给用户，让其自己决定
        return '确认要离开？';
    }
	sub(ev){
		ev.preventDefault();
		const username=ev.target.elements[0].value;
		console.log(username); //此为text中输入的值
		console.log(hashHistory.getCurrentLocation().pathname)//这是当前页面的地址 第一次点击Go按钮出来的值为/test
		console.log(window.location)//当前页面的总属性包括pathname，hash等等
		const path=`/test/${username}`;
		
		if(path==hashHistory.getCurrentLocation().pathname){ //判断当前的地址是否跟要去的值一样，一样的话输出1
			console.log(1);
		}
		hashHistory.push(path);
	}
	
  render() {
  	
    return (
      <div className="Test">
      <p>这是test网页内容</p>
      <p>定义了一个函数在text中输入123是无法去往/test/123页面的会直接跳往首页即'/'页面</p>
      <Link to='/test/example1' activeClassName="active">qu example1</Link>{'  '}
      <Link to='/test/example2' activeClassName="active">qu example2</Link>{'  '}
      <Link to='/test/example3' activeClassName="active">qu example3</Link>{'  '}
      
      <form onSubmit={this.sub} style={{margin:'10px'}}>
        <input type='text' placeholder='请输入网址的后缀name' style={{marginRight:'10px'}}/>
        <input type='submit' value='Go'/>
      </form>
      
      {
        	this.props.children || 
        	<h2>
        	  example1
        	</h2>
        } 
      </div>
    );
  }
}

export default Test;



